<template>
	<view>
		<view class="logo">
			<view class="img">
				<image
					mode="widthFix"
					src="data:image/png;base64,"
				></image>
			</view>
		</view>
		<!-- 账号密码输入框 -->
		<view class="form">
			<view class="username"><input placeholder="请输入手机号" v-model="phoneNumber" placeholder-style="color: rgba(255,255,255,0.8);" /></view>
			<view class="password"><input placeholder="请输入密码" v-model="passwd" password="true" placeholder-style="color: rgba(255,255,255,0.8);" /></view>
			<view class="btn" @tap="doLogin">登 录</view>
			<view class="res">
				<view @tap="toPage('register')">用户注册</view>
				<view @tap="toPage('resetpasswd')">忘记密码</view>
			</view>
		</view>
		<!-- 第三方登录 -->
		<!-- #ifdef MP-WEIXIN -->
		<view class="oauth">
			<view class="text">— 快速登录 —</view>
			<view class="list"><button open-type="getUserInfo" @getuserinfo="getUserInfo" class="icon weixin"></button></view>
		</view>
		<!-- #endif -->
	</view>
</template>

<script>
import md5 from '@/common/SDK/md5.min.js';
export default {
	data() {
		return {
			phoneNumber: '',
			passwd: ''
		};
	},
	methods: {
		getUserInfo(e) {
			const userinfo = e.detail.rawData;
			uni.setStorageSync('userinfo', userinfo);
			uni.navigateBack({
				delta: 1
			});
		},
		toPage(page) {
			uni.hideKeyboard();
			uni.navigateTo({
				url: page
			});
		},
		doLogin() {
			uni.hideKeyboard();
			//验证手机号码
			if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber)) {
				uni.showToast({ title: '请填写正确手机号码', icon: 'none' });
				return false;
			}
			uni.showLoading({
				title: '提交中...'
			});
			//模板示例比对本地储存的用户信息，实际使用中请替换为上传服务器比对。
			setTimeout(() => {
				let md5PW = md5(this.passwd);
				uni.getStorage({
					key: 'UserList',
					success: res => {
						for (let i in res.data) {
							let row = res.data[i];
							if (row.username == this.phoneNumber) {
								uni.hideLoading();
								//比对密码
								if (md5PW == res.data[i].passwd) {
									uni.showToast({ title: '登录成功', icon: 'success' });
									setTimeout(() => {
										uni.switchTab({
											url: '/pages/tabBar/user/user'
										});
									}, 2000);
								} else {
									uni.showToast({ title: '账号或密码不正确', icon: 'none' });
								}
							}
						}
					},
					fail: function(e) {
						uni.hideLoading();
						uni.showToast({ title: '手机号码未注册', icon: 'none' });
					}
				});
			}, 1000);
		}
	}
};
</script>

<style lang="scss">
@import '../../static/css/login.scss';
button {
	background-color: #f06c7a;
	border: none;
}
.form {
	.res {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100upx;
		color: rgba($color: #ffffff, $alpha: 0.8);
	}
}
.oauth {
	@media all and (max-height: 150vw) {
		display: block;
	}
	position: absolute;
	bottom: 50upx;
	width: 100%;
	.text {
		width: 100%;
		height: 60upx;
		display: flex;
		justify-content: center;
		align-items: center;
		color: rgba($color: #ffffff, $alpha: 0.8);
		font-size: 28upx;
	}
	.list {
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20upx 0;
		.icon {
			font-size: 80upx;
			margin: 0 30upx;
		}
	}
}
</style>
